
.game-layout {
    display: flex;
    justify-content: space-between;
  }
  
  .game-board {
    flex: 1;
    margin-right: 20px; 
  }
  
  .game-info {
    flex: 1;
  }
  
  .inventories {
    display: grid;
    grid-template-columns: repeat(3, 1fr); 
    gap: 10px; 
  }
  
  
  