

.field-grid {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 10px;
  }
  
  .field-row {
    display: flex;
  }
  
  .field-cell {
    width: 50px; 
    height: 50px; 
    border: 1px solid black;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 2px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .player1-cell {
    background-color: red; 
  }
  
  .player2-cell {
    background-color: blue; 
  }
  
  
  